<template>
  <div>
    <HeaderView />
    <main>
      <div class="notice_container">
        <div class="notice_navbar">
          <div class="notice_nav__menu">
            <ul>
              <router-link to="/mypage/resume">
                <li>
                  <img
                    src="@/assets/images/icons/menuIcon_user.png"
                    alt=""
                    class="component-column--img-black"
                  />
                  <img
                    src="@/assets/images/icons/menuIcon_user_hover.png"
                    alt=""
                    class="component-column--img-blue"
                  />
                  마이페이지
                </li>
              </router-link>
              <router-link to="/success">
                <li>
                  <img
                    src="@/assets/images/icons/menuIcon_search.png"
                    alt=""
                    class="component-column--img-black"
                  />
                  <img
                    src="@/assets/images/icons/menuIcon_search_hover.png"
                    alt=""
                    class="component-column--img-blue"
                  />
                  합격자조회
                </li>
              </router-link>
              <router-link to="/notice">
                <li class="active">
                  <img
                    src="@/assets/images/icons/menuIcon_notice.png"
                    alt=""
                    class="component-column--img-black"
                  />
                  <img
                    src="@/assets/images/icons/menuIcon_notice_hover.png"
                    alt=""
                    class="component-column--img-blue"
                  />
                  면접 공지
                </li>
              </router-link>
            </ul>
          </div>
        </div>
        <section class="notice-adm">
          <article class="notice-adm__interview">
            <div class="notice-adm__interview-title">
              <h1>면접 공지사항</h1>
            </div>
            <hr />
            <div class="notice-adm__interview-tab-header">
              <div class="notice-adm__interview-tab-header-div">
                <p>번호</p>
              </div>
              <hr />
              <div class="notice-adm__interview-tab-header-div">
                <p>제목</p>
              </div>
              <hr />
              <div class="notice-adm__interview-tab-header-div">
                <p>날짜</p>
              </div>
              <hr />
              <div class="notice-adm__interview-tab-header-div">
                <p>조회수</p>
              </div>
            </div>

            <div class="notice-adm__interview-table-notice">
              <div class="notice-adm__interview-table-notice-no">
                <p>공지</p>
              </div>
              <div class="notice-adm__interview-table-notice-title">
                <p>면접 지원 안내 및 주의사항</p>
              </div>
              <div class="notice-adm__interview-table-notice-date">
                <p>2022/01/01</p>
              </div>
              <div class="notice-adm__interview-table-notice-views">
                <p>1596</p>
              </div>
            </div>

            <div v-if="splitlist">
              <router-link
                :to="`/notice/detail/${notice.number}`"
                :key="index"
                v-for="(notice, index) in splitlist"
              >
                <div class="notice-adm__interview-table-text">
                  <div class="notice-adm__interview-table-text-no">
                    <p>{{ notice.number }}</p>
                  </div>
                  <div class="notice-adm__interview-table-text-title">
                    <p>{{ notice.title }}</p>
                  </div>
                  <div class="notice-adm__interview-table-text-date">
                    <p>{{ notice.date }}</p>
                  </div>
                  <div class="notice-adm__interview-table-text-views">
                    <p>{{ notice.view }}</p>
                  </div>
                </div>
              </router-link>
            </div>

            <div class="notice__interview-page">
              <div class="notice__interview-pagination">
                <a>&laquo;</a>
                <a
                  v-for="unit in page"
                  :key="`page-${unit}`"
                  @click="pagination(unit)"
                >
                  {{ unit }}
                </a>
                <a>&raquo;</a>
              </div>
            </div>
          </article>
        </section>
      </div>
    </main>
    <FooterView />
  </div>
</template>

<script>
import HeaderView from "@/components/HeaderView.vue";
import FooterView from "@/components/FooterView.vue";

export default {
  components: {
    HeaderView,
    FooterView,
  },
  data() {
    return {
      noticelist: [],
      splitlist: [],
      pagecount: 10,
    };
  },
  computed: {
    page() {
      return Math.ceil(this.noticelist.length / 10);
    },
  },

  async created() {
    const noticeText = await this.$axios.get(
      "https://667e891c-ab9d-4b30-b8f7-37bd394933f3.mock.pstmn.io/noticeapi/list"
    );
    this.noticelist = noticeText.data.noticelist;
    console.log(this.noticelist);
    this.pagination(1);
  },
  methods: {
    pagination(num) {
      let start = 0;
      let end = this.pagecount;
      if (num === 1) {
        this.splitlist = this.noticelist.filter(
          (v, i) => i >= start && i < end
        );
      } else {
        start = this.pagecount * (num - 1);
        end = this.pagecount * num;
        this.splitlist = this.noticelist.filter(
          (v, i) => i >= start && i < end
        );
      }
    },
  },
};
</script>

